<template>
  <div class="signup">
    <div class="main-login">
      <div class="container">
        <h2>Let's create a new account !</h2>
        <div class="login-item">
          <form @submit.prevent class="form form-login">
            <div class="form-field">
              <label class="user" for="login-username"
                ><span class="hidden">Email</span></label
              >
              <input
                id="login-username"
                type="text"
                v-model="email"
                class="form-input"
                placeholder="Email"
                required
              />
            </div>

            <div class="form-field">
              <label class="lock" for="login-password"
                ><span class="hidden">Password</span></label
              >
              <input
                id="login-password"
                type="password"
                v-model="password"
                class="form-input"
                placeholder="Password"
                required
              />
            </div>

            <div class="form-field">
              <input @click="signup" type="submit" value="Sign Up" />
            </div>
            <div class="form-field">
              <p>
                or go back to
                <router-link :to="{ name: 'Login' }">login</router-link>
              </p>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Signup",
  components: {},
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    signup: function () {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          (user) => {
            alert(`Your account has been created ! ${user}`);
            this.$router.replace("home");
          },
          (err) => {
            alert("Oops. " + err.message);
          }
        );
    },
  },
};
</script>

